.Header {
    display: flex;
    position: fixed;
    width: 100%;
    /* border: 2px solid greenyellow; */
}

.HeaderL {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    height: 100px;
    /* border: 2px solid yellow; */
}

.HeaderL img {
    width: auto;
    height: 70%;
    max-width: 100%;
    max-height: 100%;
}

@media screen and (max-width: 1200px) {
    .HeaderL {
        width: 30%;
    }
}

@media screen and (max-width: 500px) {
    .HeaderL {
        width: 50%;
    }
}