.Monitor {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}

.MonitorBoxes {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 70%;
    /* border: 2px solid red; */
}

.MonitorPreview {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.MonitorPreview img {
    width: 70%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    border: 20px solid black;
    border-radius: 100%;
}

.MonitorWag {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* border: 2px solid white; */
}

.MonitorWag h2 {
    color: white;
    text-transform: uppercase;
    font-size: 3rem;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}

.MonitorWag p {
    color: white;
    font-size: 1.7rem;
    /* -webkit-text-stroke: 1px black;
    text-fill-color: white; */
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}

.MonitorButton {
    display: flex;
    justify-content: space-around;
    width: 60%;
    /* border: 2px solid rgb(255, 0, 0); */
}

.MonitorButton a {
    color: white;
    background-color: #fed942;
    border: 3px solid black;
    box-shadow: 5px 10px 0px rgb(31, 31, 31);
    border-radius: 20px;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
    font-size: 1.3rem;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 13px;
    padding-bottom: 13px;
    transition: 0.1s ease-in-out;
}

.MonitorButton a:hover {
    color: white;
    transform: translateY(7px);
    box-shadow: 2.5px 5px 0px rgb(31, 31, 31);
}

.MonitorButton a .Blocked {
    cursor: not-allowed;
}

@media screen and (max-width: 1100px) {
    .MonitorBoxes {
        width: 100%;
    }
}

@media screen and (max-width: 800px) {
    .MonitorButton {
        width: 100%;
    }
    .MoniMonitorWagtorHao h2 {
        text-align: center;
    }
}

@media screen and (max-width: 500px) {
    .Monitor {
        padding-top: 120px;
    }
    .MonitorWag h2, p {
        text-align: center;
    }
    .MonitorBoxes {
        flex-direction: column-reverse;
    }
    .MonitorPreview {
        margin-top: 50px;
    }
    .MonitorButton a {
        padding-left: 23px;
        padding-right: 23px;
        padding-top: 13px;
        padding-bottom: 13px;
    }
}