.Tokenomics {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.Box {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 70%;
    /* border: 2px solid green; */
}

.Token {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    line-height: 0%;
    /* border: 2px solid red; */
}

.Token h2 {
    color: white;
    text-transform: uppercase;
    font-size: 3rem;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}

.Token h3 {
    color: white;
    text-transform: uppercase;
    font-size: 2.6rem;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}

.TokenBoxes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* border: 2px solid yellow; */
}

.TokenBoxesInfo {
    display: flex;
    flex-direction: column;
    flex-basis: calc(50% - 90px);
    margin: 30px;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: 3px solid black;
    box-shadow: 5px 10px 0px rgb(31, 31, 31);
    border-radius: 50px;
    background-color: #fed942;
    font-size: 1.3rem;
    padding-top: 13px;
    padding-bottom: 33px;
    transition: 0.1s ease-in-out;
    line-height: 0%;
}

.TokenBoxesInfo:hover {
    color: white;
    transform: translateY(7px);
    box-shadow: 2.5px 5px 0px rgb(31, 31, 31);
}

.TokenBoxesInfo p{
    color: white;
    font-size: 2.5rem;
        text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}

.TokenBoxesInfo span {
    color: white;
    font-size: 1.5rem;
        text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8),
    0px 0px 20px rgba(0, 0, 0, 0.8),
    0px 0px 30px rgba(0, 0, 0, 0.8),
    3px 3px 0px rgba(0, 0, 0, 0.6);
}



.Tokenimage {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
}

.Tokenimage img {
    width: 70%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    border: 20px solid black;
    border-radius: 100%;
}

@media screen and (max-width: 1100px) {
    .Box {
        width: 100%;
    }
}

@media screen and (max-width: 800px){
    .Box {
        flex-direction: column;
    }
    .Token {
        padding-bottom: 30px;
    }
}

@media screen and (max-width: 500px){
    .TokenBoxesInfo {
        flex-basis: calc(50% - 30px);
        margin: 10px;
        padding-top: 13px;
        padding-bottom: 33px;
    }
}

@media screen and (max-width: 400px){
    .TokenBoxesInfo {
        flex-basis: calc(50% - 30px);
        margin: 7px;
        padding-top: 13px;
        padding-bottom: 33px;
    }
}

@media screen and (max-width: 380px){
    .TokenBoxesInfo {
        flex-basis: calc(50% - 30px);
        margin: 4px;
        padding-top: 13px;
        padding-bottom: 33px;
    }
    .TokenBoxesInfo span {
        font-size: 1.3rem;
    }
    .Token h3 {
        font-size: 2rem;
    }
}

@media screen and (max-width: 330px){
    .TokenBoxesInfo span {
        font-size: 1rem;
    }
}